import searchBar from '@/components/uni-search/search-bar'
import productCategory from '@/components/product-category/productCategory'
import interfaces from '@/utils/interfaces.js'
export default {
	components: {
		searchBar,
		productCategory,
	},
	data() {
		return {
			searchVal: '', //搜索关键字
			categoryList: [], //产品类目
			backgroundColor: "#F8F8F8", //修改类目背景色
			type:"",
			cate_id:""
		}
	},
	onLoad(option) {
		this.initData();
		this.type=option.type;//发布类型
	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getCategroyData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						id: '',
					}
				},
				success: ((res) => {
					this.categoryList = res.data;
					//console.log(this.categoryList.child);
				})
			});
		},
		search(res) {
			this.searchVal = res.value
		},
		updateValue(item){
			//console.log(item.id);
			this.cate_id=item.id;				
		},
		stepBtn() { //下一步
			if(!this.cate_id==''){
			uni.navigateTo({
				url: '/pages/publish/publishBuy/publishTwo?type='+this.type+'&cate_id='+this.cate_id
			});
			}else{
				uni.showToast({
					title: '请选择品类',
					icon: "none"
				});
			}
		},

	}
}
